import uuid from 'uuid/v4'

import React, { useState } from 'react'
import { useDispatch } from 'react-redux'

import { addTodoAction } from '../../actions/index'

export default function TodoInput() {

    const dispatch = useDispatch()
    const [todo, setTodo] = useState('')
    const addTodo = todo => dispatch(addTodoAction(todo))

    const onInputChange = e => { setTodo(e.target.value) }

    const onSubmit = form => {
        form.preventDefault()
        if (todo.trim() === '') return

        addTodo({
            id: uuid(),
            name: todo,
            isComplete: false
        })

        setTodo('')
    }

    return (
        <form className="text-center border border-light p-3 mb-4" onSubmit={onSubmit}>

            <p className="h4 mb-4">Todo List - Redux Demo</p>

            <input
                type="text"
                name="todo"
                placeholder="Add a todo"
                value={todo}
                onChange={onInputChange}
                className="form-control mb-4"
            />

            <button className="btn btn-blue-grey" type="submit">Add Todo</button>
            
        </form>
    )

}
