import React from 'react'
import { useDispatch, useSelector } from 'react-redux'

import { deleteTodoAction, toggleTodoAction } from '../../actions/index'

export default function TodoList() {

    const dispatch = useDispatch()   
    const todos = useSelector(store => store.todos_R.todos)
    
    const toggleTodo = todoId => dispatch(toggleTodoAction(todoId))
    const deleteTodo = todoId => dispatch(deleteTodoAction(todoId))

    return (
        <ul className="list-group mb-4">
            {todos.map(todo => (
                <li key={todo.id} className="list-group-item">
                    <input
                        type="checkbox"
                        checked={todo.isComplete}
                        onChange={toggleTodo.bind(this, todo.id)}
                    />
                    <span className={todo.isComplete ? 'complete' : null}> {todo.name}</span>
                    <button
                        type="button"
                        className="close"
                        aria-label="Close"
                        onClick={deleteTodo.bind(this, todo.id)}>
                            <span aria-hidden="true">&times;</span>
                    </button>
                </li>
            ))}
        </ul>
    )

}
